@import 'includes/master/mixins';
.coming-soon-banner {
    .content {
        .title{
            font-size: 2.92vw;
        }
        .text{
            font-size: 1.95vw;
        }
    }
}
.banner-mid-coming{
    min-height: 300px;
}
.right{
    .res-menu-icon{
        width: 30px;
        display: none;
    }
}
header{
    width: 100%;
}
@media screen and (max-width: 1480px){
    header .menu{
        li.th-lang, li.en-lang{
            padding: 0;
        }
    }
}
@media screen and (max-width: 1200px){
    header{
        padding: 0 48px;
    }
    .form-comingsoon{
        padding: 48px;
    }
    .article-list{
        padding: 48px 33px;
        
    }
    .article{
        padding: 0;
    }
}
@media screen and (max-width: 992px){
    .article-detail{
        padding: 33px;
        .text, .title{
            padding: 0;
        }
        img{
            width: 100%;
        }
    }
    footer{
        padding: 33px;
        padding-bottom: 15px;
        .copy-right{
            padding-top: 15px;
            padding-bottom: 0;
            .left{
                display: block;
                position: relative;
                .logo{
                    position: absolute;
                    top: 10px;
                }
                .link{
                    width: 100%;
                    margin-top: 4px;
                    margin-left: 0;
                    padding-left: 30px;
                }
                p{
                    padding-left: 30px;
                }
            }
        }
    }
    .banner-mid-coming{
        .content{
            padding: 0 80px;
            .sub-title{
                font-size: 18px;
            }
            .title{
                font-size: 36px;
                line-height: 36px;
            }
        }
    }
    .coming-soon-banner {
        .content {
            padding: 0 15px;
            .title{
                font-size: 36px;
            }
            .text{
                font-size: 30px;
            }
        }
    }
    .form-comingsoon{
        .title, .text-sub{
            font-size: 36px;
        }
        .sub-title{
            font-size: 18px;
        }
        .text{
            font-size: 18px;
        }
        input, button{
            font-size: 16px;
        }
    }
    .article-list{
        .col-12:last-child{
            padding: 0;
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            max-width: 100%;
            .article{
                width: 50%;
                float: left;
                padding: 15px;
            }
        }
    }
}
@media screen and (max-width: 768px){
    .article-detail{
        .text{
            font-size: 16px;
            line-height: 24px;
        }
        .title{
           font-size: 24px;
           line-height: 50px;
        }
        img{
            margin: 15px 0;
        }
        .btn-blog{
            margin-bottom: 10px;
            svg{
                width: 30px;
                min-width: 20px;
            }
            &.left{
                svg{
                    margin-left: 0;
                }
            }
        }
        .btn-bottom {
            .btn-blog{
                margin-top: 50px;
                margin-bottom: 0;
                svg{
                    width: 30px;
                    min-width: 20px;
                }
                &.right{
                    svg{
                        margin-right: 0;
                    }
                }
                &.left{
                    svg{
                        margin-left: 0;
                    }
                }
                img{
                    display: none;
                }
            }
        }
    }
    footer{
        padding: 13px;
    }
    header{
        padding: 0 30px;
        .right{
            .btn-subscribe-header{
               position: absolute;
               top: 75px;
               margin-right: 0 !important;
               #btn-subscribe-header{
                padding: 5px 9px;
               }
            }
            li.th-lang{
                white-space: nowrap;
            }
            li{
                padding: 0;
            }
        }
        &.active{
            .right{
                .res-menu-icon{
                    display: block;
                    width: 24px;
                    cursor: pointer;
                }
                .menu{
                    display: none;
                    margin-top: -32px;
                    li{
                        padding: 0;
                    }
                }
                .btn-subscribe-header{
                   top: 32px;
                }
                
            }
            &.show-menu{
                padding-top: 18px;
                padding-bottom: 18px;
                .right{
                    .res-menu-icon{
                        display: none;
                    }
                    .menu{
                        @include flexbox();
                    }
                }
            }
        }
        
    }
    .banner-mid-coming{
        .content{
            padding: 0 30px;
        }
    }
    .form-comingsoon{
        .title, .text-sub{
            font-size: 24px;
        }
        .sub-title{
            font-size: 16px;
        }
        .text{
            font-size: 16px;
        }
        input, button{
            font-size: 14px;
        }
    }
    .article-list{
        padding: 30px 13px;
        .article{
            margin-bottom: 15px;
            .content{
                padding: 15px;
                box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
            }
        }
        .col-12:last-child{
            padding: 0 15px;
            .article{
                width: 100%;
                float: unset;
                padding: 0;
            }
        }
    }
}
@media screen and (max-width: 480px){
    .article-detail{
        padding: 30px 0;
        .btn-blog{
            font-size: 14px;
            svg{
                width: 20px;
                min-width: 20px;
            }
            &.left{
                svg{
                    margin-right: 4px;
                }
            }
        }
        .btn-bottom {
            .btn-blog{
                margin-top: 30px;
                font-size: 14px;
                svg{
                    width: 20px;
                    min-width: 20px;
                }
                &.right{
                    svg{
                        margin-left: 4px;
                    }
                }
                &.left{
                    svg{
                        margin-right: 4px;
                    }
                }
                img{
                    display: none;
                }
            }
        }
    }
    footer{
        padding: 0;
        .copy-right{
            padding-bottom: 15px;
            padding-top: 40px;
            .left{
                .logo{
                    top: -30px;
                }
                p, .link{
                    padding-left: 0;
                }
            }
            .right{
                position: absolute;
                top: 10px;
                right: 15px;
            }
            p, .link{
                font-size: 13px;
            }
        }
    }
    .banner-mid-coming{
        .content{
            padding: 0 15px;
            .sub-title{
                font-size: 16px;
            }
            .title{
                font-size: 24px;
                line-height: 30px;
            }
            .text{
                font-size: 14px;
            }
        }
        
    }
    .form-comingsoon{
        padding: 30px 15px;
        .sub-title{
            font-size: 14px;
        }
        .text{
            font-size: 16px;
            line-height: 24px;
        }
        button{
            img{
                display: none;
            }
        }
    }
    .coming-soon-banner {
        .content {
            .title{
                font-size: 30px;
            }
            .text{
                font-size: 24px;
            }
        }
    }
    .article-list{
        padding: 30px 0px;
        
    }
}
@media screen and (max-width: 375px){
    .form-comingsoon{
        input{
            font-size: 12px;
            padding: 5px 10px;
        }
        button{
            padding: 0 10px;
            font-size: 12px;
        }
    }
}
@media screen and (max-width: 350px){
    header .left{
        padding-top: 60px;
    }
}
